import React from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import Home from './components/pages/Home'
import About from './components/pages/About'
import Psych from './components/pages/Psych'
import Society from './components/pages/Society'
import Sources from './components/pages/Sources'



function App() {
  return (
    <div className="App">
        <Router>
          <Navbar />
            <Switch>
              <Route path='/' exact component={Home} />
              <Route path='/about' exact component={About} />
              <Route path='/psych' exact component={Psych} />
              <Route path='/society' exact component={Society} />
              <Route path='/sources' exact component={Sources} />
            </Switch>
        </Router>
    </div>
    
    
  );
}

export default App;
